@tailwind base;
@tailwind components;

/* === GLOBAL === */

html,
body,
#root {
    @apply h-full;
}

.is-dark {
    @apply bg-gray-700 text-gray-100;
}

/* === TYPOGRAPHY === */

h1,
h2,
h3 {
    @apply font-bold;
}

h4,
h5,
h6 {
    @apply font-medium;
}

h1 {
    @apply text-3xl;
}

h2 {
    @apply text-2xl;
}

h3 {
    @apply text-xl;
}

h4 {
    @apply text-lg;
}

h5 {
    @apply text-base;
}

h6 {
    @apply text-base;
}

/* === BUTTONS === */

.button {
    @apply bg-white text-gray-600 border border-gray-600 font-bold py-2 px-4 rounded shadow-md;
}

.button:hover {
    @apply bg-gray-100;
}

.button:focus {
    @apply outline-none shadow-outline;
}

.button.is-primary {
    @apply text-white bg-blue-500 border-blue-600;
}

.button.is-primary:hover {
    @apply bg-blue-600;
}

.button.is-danger {
    @apply text-white bg-red-500 border-red-600;
}

.button.is-danger:hover {
    @apply bg-red-600;
}

.button.is-warning {
    @apply text-white bg-yellow-500 border-yellow-600;
}

.button.is-warning:hover {
    @apply bg-yellow-600;
}

.button.is-success {
    @apply text-white bg-green-500 border-green-600;
}

.button.is-success:hover {
    @apply bg-green-600;
}

/* === INPUTS === */

.input-text {
    @apply border-solid border-gray-600 border-2 shadow-md rounded-lg px-4 py-2;
}

.select {
    /* @apply block appearance-none bg-white border border-gray-400 px-4 py-2 pr-8 rounded shadow leading-tight; */
    @apply block appearance-none w-full bg-white border border-gray-400 px-4 py-2 pr-8 rounded shadow leading-tight;
}

.select.fullwidth {
    @apply w-full;
}

/* === DOCS === */

.example-heading {
    @apply font-bold;
}

/* === UTILS === */

@tailwind utilities;

